import React, { useCallback, useEffect, useState } from "react";

export const usePrevNextButtons = (emblaApi) => {
  const [prevBtnDisabled, setPrevBtnDisabled] = useState(!emblaApi?.canScrollPrev());
  const [nextBtnDisabled, setNextBtnDisabled] = useState(!emblaApi?.canScrollNext());

  const onPrevButtonClick = useCallback(() => emblaApi?.scrollPrev(), [emblaApi]);
  const onNextButtonClick = useCallback(() => emblaApi?.scrollNext(), [emblaApi]);

  useEffect(() => {
    if (!emblaApi) return;
    
    const onSelect = () => {
      setPrevBtnDisabled(!emblaApi.canScrollPrev());
      setNextBtnDisabled(!emblaApi.canScrollNext());
    };

    emblaApi.on("reInit", onSelect).on("select", onSelect);
    onSelect();

    return () => emblaApi.off("reInit", onSelect).off("select", onSelect);
  }, [emblaApi]);

  return {
    prevBtnDisabled,
    nextBtnDisabled,
    onPrevButtonClick,
    onNextButtonClick,
  };
};

export const PrevButton = React.memo(({ disabled, ...props }) => {
  const className = `inline-block ${disabled ? "" : "gradientBorderLg"}`;

  return (
    <div className={className}>
      <button
        {...props}
        disabled={disabled}
        type="button"
        className="cardGradient rounded-lg w-16 h-16 flex items-center justify-center
                   disabled:opacity-50 cursor-pointer disabled:cursor-not-allowed"
        aria-label="Previous"
      >
        <svg className="w-5 h-5 arrowIcon" viewBox="0 0 532 532">
          <path fill="currentColor" d="M355.66 11.354c13.793-13.805 36.208-13.805 50.001 0 13.785 13.804 13.785 36.238 0 50.034L201.22 266l204.442 204.61c13.785 13.805 13.785 36.239 0 50.044-13.793 13.796-36.208 13.796-50.002 0a5994246.277 5994246.277 0 0 0-229.332-229.454 35.065 35.065 0 0 1-10.326-25.126c0-9.2 3.393-18.26 10.326-25.2C172.192 194.973 332.731 34.31 355.66 11.354Z" />
        </svg>
      </button>
    </div>
  );
});

export const NextButton = React.memo(({ disabled, ...props }) => {
  const className = `inline-block ${disabled ? "" : "gradientBorderLg"}`;

  return (
    <div className={className}>
      <button
        {...props}
        disabled={disabled}
        type="button"
        className="cardGradient rounded-lg w-16 h-16 flex items-center justify-center
                   disabled:opacity-50 cursor-pointer disabled:cursor-not-allowed"
        aria-label="Next"
      >
        <svg className="w-5 h-5 arrowIcon" viewBox="0 0 532 532">
          <path
            fill="currentColor"
            d="M176.34 520.646c-13.793 13.805-36.208 13.805-50.001 0-13.785-13.804-13.785-36.238 0-50.034L330.78 266 126.34 61.391c-13.785-13.805-13.785-36.239 0-50.044 13.793-13.796 36.208-13.796 50.002 0 22.928 22.947 206.395 206.507 229.332 229.454a35.065 35.065 0 0 1 10.326 25.126c0 9.2-3.393 18.26-10.326 25.2-45.865 45.901-206.404 206.564-229.332 229.52Z"
          />
        </svg>
      </button>
    </div>
  );
});




